let leftsearchSchema = [
     {
    fieldName: 'Approval Group',
    fieldType: "text",
    fieldIcon: "fa fa-check-circle",
    paramName:"groupName",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: []
},

{
    fieldName: 'User',
    fieldType: "ddl-check",
    fieldIcon: "fa fa-users",
    paramName:"userIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: []
},
{
    fieldName: 'Status',
    fieldType: "ddl-check",
    fieldIcon: "fa fa-bar-chart",
    paramName:"statusIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: [
        {value:'1001',name:'ACTIVE'},
    {value:'1002',name:'INACTIVE'}
    ]
}]
function bindleftCommonSearchdropdown(instance,modulename, filtername){
    var vm = instance;
    var TagData = [];
    if (filtername == "APPROVALRULE") {
        let obj = {};
        var url=`isIncludeLoginUserId=1&approvalGroupIds=&approvalChainIds=&moduleName=${filtername}&DepartmentIds`; 
        DataService.LoadUsersByCompanyId(url).then(response=>{     
        var json= response.data;
        if(json !=null){   
            json.forEach(function (item, index){
                 obj={
                    name:item.USERNAME,
                    value:`${item.USER_ID}`
                }     
                TagData.push(obj); 
            });     
            vm.leftSearchFields[1].listOptions= TagData;              
        }
    });
 }
}
export default { buttons, leftsearchSchema,bindleftCommonSearchdropdown};