<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Approval"
            SubModuleCode="Approval" IdentityColumn="GROUPID" :HeaderText="$t('ApprovalGroup')" :ListData="GroupData"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                    <template v-if="data.row.SCOUNT == 0 && data.row.DCOUNT == 0">
                        <td class="text-center single-action" :title="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'">
                            <div class="cstm-drop-btn">
                                <select v-model="data.row.STATUSID"
                                    @change="DropdownUpdateStatus(data.row.STATUSID, data.row.GROUPID);"
                                    :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                    <option value="1001" selected="selected">Active</option>
                                    <option value="1002">Inactive</option>
                                </select>
                            </div>
                        </td>
                    </template>

                    <template v-else>
                        <td class="text-center single-action" :title="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'">
                            <div class="cstm-drop-btn">
                                <select v-model="data.row.STATUSID" disabled
                                    :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                    <option value="1001" selected="selected">Active</option>
                                    <option value="1002">Inactive</option>
                                </select>
                            </div>
                        </td>
                    </template>

                </template>
                <template v-if="data.column.COLUMN_NAME == 'GROUPNAME'">
                    <span class="text-dark" v-if="data.row.GROUPNAME">
                        <a href="javascript:;" v-on:click="EditGroup(data.row.GROUPID)" :id="data.row.GROUPID"><em
                                class="text-primary" aria-hidden="true">{{ data.row.GROUPNAME
                                }}</em></a>
                    </span>

                </template>
            </template>
        </tg-list>
    </section>
</template>
    
<script>
import DataService from '../../service/DataService';
import Schema from '../../views/ApprovalGroup/ApprovalGroupListSchema'

export default {

    data() {
        return {
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": 'GROUPNAME',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ApprovalGroupName'),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true,
                }

            },
            {
                "COLUMN_NAME": 'USERS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Users'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": 'DESCRIPTION',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Description'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'STATUS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true,
                }
            },

            ],
            noRecord: true,
            userIds: '',
            groupName: '',
            statusIds: '',
            SortBy: '',
            SortExp: '',
            IsDefault: '',
            GroupData: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            StatusData: [],
            listheaderbuttons: Schema.buttons,
            DetailListType: 'List',
            isPartial: 0,
            search: ''
        }
    },
    created() {
        this.FetchData();
        Schema.bindleftCommonSearchdropdown(this, 'Approval', 'APPROVALRULE');
        Schema.bindleftCommonSearchdropdown(this, 'Approval', 'Status');
    },
    methods: {
        async FetchData() {
            var vm = this;
            var url = `${vm.searchCondition}&userIds=${vm.userIds}&groupName=${vm.groupName}&statusIds=${vm.statusIds}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&isPartial=${vm.isPartial}`;
            await DataService.ApprovalGroupListing(url).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.GroupData = response.data;
                        vm.GroupData.forEach((row) => {
                            row.isCheckBoxDisabled = false;
                            if (row.DCOUNT == 0 && row.SCOUNT == 0) {
                                row.isCheckBoxDisabled = false;
                            } else {
                                row.isCheckBoxDisabled = true;
                            }
                        })
                        vm.TotalRecords = response.data[0].TOTALRECORDS
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    } else {
                        vm.GroupData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.GroupData = [];
                    vm.TotalRecords = 0
                }
                setTimeout(function () {
                    vm.CheckBoxBootstrap()
                }, 100);
            });
            vm.isLoading = false;
        },
        DropdownUpdateStatus(statusId, id) {
            var vm = this;
            vm.isLoading = true;
            vm.confirmR(vm.$t('Are you sure you want to update this record?'), true, false, function (result) {
                var url = `id=${id}&statusId=${statusId}&tablename=TALYGEN_APPROVAL_GROUP&columnname=APPROVAL_GROUP_ID`;
                DataService.UpdateStatus(url).then(response => {
                    var status = ''
                    vm.ShowAlert(vm.$t('UpdateStatus'), "success", ('Alert'));
                    vm.FetchData();
                    vm.isLoading = false;
                })
            })
            vm.FetchData();
            vm.isLoading = false;
        },
        DeleteGroup() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  "), function () {
                    vm.isLoading = true;
                    var url = `ids=${val}`
                    DataService.DeleteGroup(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ApprovalGroup')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "DeleteGroup":
                    this.DeleteGroup()
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "AddNewGroup":
                    this.AddNewGroup()
                    break;
            }
        },
        AddNewGroup: function () {
            this.$router.push({
                name: 'Add Approval Group',
            });
        },
        EditGroup: function (GROUPID) {
            this.$router.push({
                name: 'Add Approval Group',
                params: {
                    id: GROUPID
                }
            });
        },
        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
}
</script>
    